  /* Ensure the CSS is applied correctly */
.firstBox {
  display: flex;
  flex-direction: row; /* Ensure flex direction is row for larger screens */
  align-items: center;
  padding: 20px;
  background-color: #333;
  color: white;
  border-radius: 8px;
  flex-wrap: wrap; /* Allow the content to wrap within the box */
  overflow: hidden; /* Prevent overflow */
}

.docImg {
  width: 100px;
  height: 100px;
  object-fit: cover;
  border-radius: 50%;
  margin-right: 20px;
  flex-shrink: 0; /* Prevent the image from shrinking */
}

.content {
  display: flex;
  flex-direction: column;
  flex: 1; /* Allow the content to take up the remaining space */
  overflow-wrap: break-word; /* Ensure long words break appropriately */
}

