@media (max-width: 768px) {
  .banner table, .banner thead, .banner tbody, .banner th, .banner td, .banner tr {
    display: block;
  }

  .banner tr {
    margin-bottom: 15px;
  }

  .banner th, .banner td {
    text-align: right;
    padding-left: 50%;
    position: relative;
  }

  .banner th::before, .banner td::before {
    content: attr(data-label);
    position: absolute;
    left: 10px;
    width: calc(50% - 20px);
    padding-right: 10px;
    text-align: left;
    font-weight: bold;
  }
}

