/* Reset some default styles */
* {
    box-sizing: border-box;
    margin: 0;
    padding: 0;
    font-family: 'Arial', sans-serif;
  }
  
  .navbar {
    background:  #e5e5e5;
    height: 80px;
    display: flex;
    /* justify-content: center; */
    align-items: center;
  }
  
  .navbar-container {
    display: flex;
    justify-content: space-between;
    height: 80px;
    z-index: 11;
    width: 100%; 
    max-width: 1000px; 
  }
  
  .navbar-logo {
    color: black;
    /* justify-self: start; */
    cursor: pointer;
    text-decoration: none;
    font-size: 1.5rem;
    display: flex;
    align-items: center;
    margin-left: 10px;
  }

  .logo-image{
    width:180px;
    height:auto;
  }
  
  .menu-icon {
    display: none;
  }
  
  .nav-menu {
    display: flex;
    align-items: center;
    list-style: none;
    text-align: center;
  }
  
  .nav-item {
    height: 50px;
    font-size: 20px;
    font-weight: bold;
  }
  
  .nav-links {
    color: #362796;
    text-decoration: none;
    padding: 0.5rem 1rem;
    height: 100%;
    display: flex;
    align-items: center;
  }
  
  .nav-links:hover {
    background-color: #362796;
    border-radius: 14px;
    color: white;
    
  }
