  @media screen and (max-width: 1120px) {
    .menu-icon {
      display: block;
      position: absolute;
      top: 10px;
      right: 20px;
      transform: translate(-100%, 60%);
      font-size: 1.8rem;
      cursor: pointer;
      z-index: 1;
    }
   
    .nav-menu {
      display: flex;
      flex-direction: column;
      width: 100%;
      height: 90vh;
      position: absolute;
      top: 80px;
      left: -100%;
      opacity: 1;
      transition: all 0.5s ease;
      background: #e5e5e5;
      z-index: 1;
      
    }
  
    .nav-menu.active {
      left: 0;
      opacity: 1;
      transition: all 0.5s ease;
      z-index: 1;
    }
  
    .nav-item {
      width: 100%;
      height: 80px;
    }
  
    .nav-links {
      text-align: center;
      padding: 2rem;
      width: 100%;
      display: table;
    }
    .login-btna{
        position: relative;
        margin-left: 80px;
     }

     .logo-image{
      width:100px;
      height:auto;
    }
  }

  .login-btn:hover{
    background-color: #422dc9;
    border: 0.02px solid #362796;
}

.login-btn {
    position: relative;
    overflow: hidden;
    cursor: pointer;
    background-color: #362796;
    color: white;
    border-radius: 20px;
    padding: 10px;
    padding-left: 35px;
    padding-right: 35px;
    border: 2px solid #0909db;
        
  }
  
  .login-btn::before {
    content: "";
    position: absolute;
    top: 0;
    left: -100%;
    width: 100%;
    height: 100%;
    background: linear-gradient(to right, transparent 0%, rgb(164, 160, 241) 50%, transparent 100%);
    animation: login-btn 1.5s infinite linear;
  }
  
  @keyframes login-btn {
    100% {
      left: 100%;
    }
  } 