.sign {
  justify-content: center;
  align-items: center;
  letter-spacing: 2;
  font-family: "Clip";
  text-transform: uppercase;
  font-size: 3em;
  color: #ffe6ff;
  text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #362796,
    -0.2rem 0.1rem 1rem #362796, 0.2rem 0.1rem 1rem #362796,
    0 -0.5rem 2rem #362796, 0 0.5rem 3rem #362796;
  animation: shine 2s forwards, flicker 3s infinite;
}
.appointmentsign{
  justify-content: center;
  align-items: center;
  margin-left: 150px;
  margin-bottom: 40px;
  letter-spacing: 2;
  font-family: "Clip";
  text-transform: uppercase;
  font-size: 3em;
  color: #ffe6ff;
  text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #362796,
    -0.2rem 0.1rem 1rem #362796, 0.2rem 0.1rem 1rem #362796,
    0 -0.5rem 2rem #362796, 0 0.5rem 3rem #362796;
  animation: shine 2s forwards, flicker 3s infinite;
}
@keyframes blink {
  0%,
  22%,
  36%,
  75% {
    color: #ffe6ff;
    text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #362796,
      -0.2rem 0.1rem 1rem #362796, 0.2rem 0.1rem 1rem #362796,
      0 -0.5rem 2rem #362796, 0 0.5rem 3rem #362796;
  }
  28%,
  33% {
    color: #362796;
    text-shadow: none;
  }
  82%,
  97% {
    color: #362796;
    text-shadow: none;
  }
}

.flicker {
  animation: shine 2s forwards, blink 3s 2s infinite;
}

.fast-flicker {
  animation: shine 2s forwards, blink 10s 1s infinite;
}

@keyframes shine {
  0% {
    color: #6b1839;
    text-shadow: none;
  }
  100% {
    color: #ffe6ff;
    text-shadow: 0 0 0.6rem #ffe6ff, 0 0 1.5rem #362796,
      -0.2rem 0.1rem 1rem #362796, 0.2rem 0.1rem 1rem #362796,
      0 -0.5rem 2rem #362796, 0 0.5rem 3rem #362796;
  }
}

